.swadist__header{
    display: flex;
    flex-direction: row;
}

.swadist__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction:column;
    margin-right: 5rem;
}

.swadist__header-content h1{
    font-weight: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.swadist__header-content p{
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-text);
    margin-top: 1.5rem;
}

.swadist__header-content__input{
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}

.swadist__header-content__input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight:400;
    font-size: 20px;
    line-height: 28px;
    background: var(--color-footer);
    border:2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    color: #3D6184;
}

.swadist__header-content__input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: #fff;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: 2px solid #FF4820;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.swadist__header-content__people{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    margin-top: 2rem;
}

.swadist__header-content__people img{
    width: 181.79px;
    height: 38px;
}

.swadist__header-content__people p{
    margin-left: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}

.swadist__header-image{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swadist__header-image img{
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px) {
    .swadist__header{
       flex-direction: column;
    }

    .sadist__header-content{
        margin: 0 0 3rem;
    }
}

@media screen and (max-width:650px) {
    .swadist__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .sadist__header p {
        font-size: 16px;
        line-height: 24px;
    }

    .swadist__header-content__people{
        flex-direction: column;
    }

    .swadist__header-content__people p{
        margin: 0;
    }

    .swadist__header-content__inupt input,
    .swadist__header-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px){
    .swadist__header-xontent h1 {
            font-size: 36px;
            line-height: 48px;
        }
    
        .sadist__header p {
            font-size: 14px;
            line-height: 24px;
        }

                .swadist__header-content__inupt input,
                .swadist__header-content__input button {
                    font-size: 12px;
                    line-height: 16px;
                }
}